}
.glow h2 {
  left: 50%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  margin-top: 20px;
  color: #fff;
  font-size: 2.4rem;
  text-align: center;
  -webkit-animation: glow 1s ease-in-out infinite alternate;
  animation: glow 2s ease-in-out infinite alternate;
  font-family: "Alex Brush", cursive;
  position: relative;
  margin: auto !important;
}
@-webkit-keyframes glow {
  from {
    text-shadow: 0 0 20px #fab507 0 0 30px #daa520, 0 0 40px #cc9303;
  }
  to {
    text-shadow: 0 0 30px #fab507, 0 0 40px #daa520, 0 0 50px #cc9303;
  }
}
@keyframes glow {
  from {
    text-shadow: 0 0 20px #fab507 0 0 30px #daa520, 0 0 40px #cc9303;
  }
  to {
    text-shadow: 0 0 30px #fab507, 0 0 40px #daa520, 0 0 50px #cc9303;
  }
}

