.btn-cont {
  text-align: center;
  margin-bottom: 100px;
  /* position: absolute; */
  /* z-index: 10; */
  /* top: 95vh; */
  margin-left: 0 auto;
  /* transform: translate(-50%, -50%); */
}
.btn-cont .btn {
  position: relative;
  box-sizing: border-box;
  padding: 10px 30px;
  border: 1px solid goldenrod;
  color: goldenrod;
  text-decoration: none;
  font-family: "Open Sans", sans-serif;
  letter-spacing: 2px;
  -webkit-font-smoothing: antialiased;
}
.btn-cont .btn:hover {
  color: goldenrod;
  border: none;
}
.btn-cont .btn:hover .line-1 {
  -webkit-animation: move1 1500ms 1 ease;
  animation: move1 1500ms ease infinite;
}
.btn-cont .btn:hover .line-2 {
  -webkit-animation: move2 1500ms 1 ease;
  animation: move2 1500ms ease infinite;
}
.btn-cont .btn:hover .line-3 {
  -webkit-animation: move3 1500ms 1 ease;
  animation: move3 1500ms ease infinite;
}
.btn-cont .btn:hover .line-4 {
  -webkit-animation: move4 1500ms 1 ease;
  animation: move4 1500ms ease infinite;
  /* animation: name duration timing-function delay iteration-count direction */
}
.btn-cont .line-1 {
  content: "";
  display: block;
  position: absolute;
  width: 1px;
  background-color: goldenrod;
  left: 0;
  bottom: 0;
}
.btn-cont .line-2 {
  content: "";
  display: block;
  position: absolute;
  height: 1px;
  background-color: goldenrod;
  left: 0;
  top: 0;
}
.btn-cont .line-3 {
  content: "";
  display: block;
  position: absolute;
  width: 1px;
  background-color: goldenrod;
  right: 0;
  top: 0;
}
.btn-cont .line-4 {
  content: "";
  display: block;
  position: absolute;
  height: 1px;
  background-color: goldenrod;
  right: 0;
  bottom: 0;
}

@-webkit-keyframes move1 {
  0% {
    height: 100%;
    bottom: 0;
  }
  54% {
    height: 0;
    bottom: 100%;
  }
  55% {
    height: 0;
    bottom: 0;
  }
  100% {
    height: 100%;
    bottom: 0;
  }
}

@keyframes move1 {
  0% {
    height: 100%;
    bottom: 0;
  }
  54% {
    height: 0;
    bottom: 100%;
  }
  55% {
    height: 0;
    bottom: 0;
  }
  100% {
    height: 100%;
    bottom: 0;
  }
}
