.hexagon {
  background-image: url(/static/media/bg-black.af046262.webp);
  width: 100px;
  height: 60px;
  background: #5f6062;
  position: relative;
  top: 100px;
  text-align: center;
  -webkit-transition: all 0.3s ease-in-out;
  transition: all 0.3s ease-in-out;
}
.hexagon:before {
  content: "";
  position: absolute;
  top: -25px;
  left: 0;
  width: 0;
  height: 0;
  border-left: 50px solid transparent;
  border-right: 50px solid transparent;
  border-bottom: 25px solid #5f6062;
  -webkit-transition: all 0.3s ease-in-out;
  transition: all 0.3s ease-in-out;
}
.hexagon:after {
  content: "";
  position: absolute;
  bottom: -24px;
  left: 0;
  width: 0;
  height: 0;
  border-left: 50px solid transparent;
  border-right: 50px solid transparent;
  border-top: 25px solid #5f6062;
  -webkit-transition: all 0.3s ease-in-out;
  transition: all 0.3s ease-in-out;
}

.hexagon:hover {
  background: #ec008c;
}

.hexagon:hover:after {
  border-top: 25px solid #ec008c;
}
.hexagon:hover:before {
  border-bottom: 25px solid #ec008c;
}

