.btn-2 {
  position: relative;
  overflow: hidden;
  -webkit-transition: 0.3s;
  transition: 0.3s;
  border: 0px;
}
button :focus {
  outline: none !important;
}
a :focus {
  outline: none !important;
}
.nav-link :focus {
  outline: none !important;
}
.btn-2:after {
  position: absolute;
  -webkit-transition: 0.3s;
  transition: 0.3s;
  content: "";
  width: 0;
  left: 50%;
  bottom: 0;
  height: 2px;
  background: #f7f7f7;
  /* border: 0px; */
}
.btn-2:hover {
  cursor: pointer;
}
.btn-2:hover:after {
  width: 100%;
  left: 0;
}
.btn-2:focus {
  box-shadow: none;
}
